import axios from 'axios';

// Verso atual do Girus-CLI
export const CURRENT_VERSION = '0.1.0';

// Constantes para gerenciamento de notificaes
export const NOTIFICATION_RESHOW_HOURS = 24; // Horas antes de mostrar a notificao novamente

// Chaves do localStorage
export const LS_KEYS = {
    LAST_CHECK: 'girus_update_last_check',
    DISMISSED_VERSION: 'girus_update_dismissed_version',
    RESHOW_HOURS: 'girus_update_reshow_hours'
};

/**
 * Obtm o tempo de reexibio da notificao (pode ser sobrescrito pelo localStorage)
 * @returns Nmero de horas antes de mostrar a notificao novamente
 */
export const getNotificationReshowHours = (): number => {
    const storedValue = localStorage.getItem(LS_KEYS.RESHOW_HOURS);
    if (storedValue) {
        const hours = parseInt(storedValue, 10);
        if (!isNaN(hours) && hours > 0) {
            return hours;
        }
    }
    return NOTIFICATION_RESHOW_HOURS;
};

// Interface para representar informaes da release
export interface ReleaseInfo {
    version: string;
    url: string;
    name: string;
    published_at: string;
    body: string;
}

/**
 * Verifica se h uma nova verso do Girus-CLI disponvel
 * @returns Promise contendo informaes da verso mais recente ou null se estiver atualizado
 */
export const checkForUpdates = async (): Promise<ReleaseInfo | null> => {
    try {
        // Obter a verso mais recente do GitHub
        const response = await axios.get('https://api.github.com/repos/badtuxx/girus-cli/releases/latest');

        // Extrair o nmero da verso (remove o 'v' inicial)
        const latestVersion = response.data.tag_name.replace(/^v/, '');

        // Comparar com a verso atual
        if (isNewerVersion(latestVersion, CURRENT_VERSION)) {
            return {
                version: latestVersion,
                url: response.data.html_url,
                name: response.data.name,
                published_at: response.data.published_at,
                body: response.data.body
            };
        }

        // Retorna null se j estiver na verso mais recente
        return null;
    } catch (error) {
        console.error('Erro ao verificar atualizaes:', error);
        return null;
    }
};

/**
 * Compara duas verses no formato semntico (x.y.z)
 * @param version1 Nova verso
 * @param version2 Verso atual
 * @returns true se version1 for mais recente que version2
 */
export const isNewerVersion = (version1: string, version2: string): boolean => {
    const v1Parts = version1.split('.').map(Number);
    const v2Parts = version2.split('.').map(Number);

    for (let i = 0; i < Math.max(v1Parts.length, v2Parts.length); i++) {
        const v1Part = v1Parts[i] || 0;
        const v2Part = v2Parts[i] || 0;

        if (v1Part > v2Part) return true;
        if (v1Part < v2Part) return false;
    }

    return false; // As verses so iguais
}; 